import React, { useState } from 'react';
import axios from 'axios';
import { setUserSession, hideLogin, showDashboard, showLogout, getURL } from './Utils/Common';

function Register(props) {
  const [loading, setLoading] = useState(false);
  const username = useFormInput('');
  const password = useFormInput('');
  const regname = useFormInput('');
  const regpassword = useFormInput('');
  const regpassword2 = useFormInput('');
  const [error, setError] = useState(null);
  
  const C_STRONGREGEX = new RegExp("^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#\$%\^&\*])(?=.{8,})");
  const C_MEDIUMREGEX = new RegExp("^(((?=.*[a-z])(?=.*[A-Z]))|((?=.*[a-z])(?=.*[0-9]))|((?=.*[A-Z])(?=.*[0-9])))(?=.{6,})");
  var FIRSTONE = "";
  var BLUR1 = false;
  var BLUR2 = false;
  var DONTCHECK = false;
  
  // handle button click of login form	// gmarklab.southafricanorth.cloudapp.azure.com
  const handleLogin = () => {
	if(regname.value == ""){
		alert("You must enter a valid email address!")
		document.all("txtEmail").focus();
		return false;
	}
	
	if (/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(regname.value)){
	}
	else {
		alert("You have entered an invalid email address!");
		document.all("txtEmail").focus();
		return false;
	}
	
	if(regpassword.value == ""){
		alert("You must enter a password!")
		document.all("txtPassword").focus();
		return false;
	}
	
	if(regpassword2.value == ""){
		alert("You must enter a password!")
		document.all("txtPassword2").focus();
		return false;
	}
	
	if(regpassword2.value !== regpassword.value){
		alert("The passwords provided do not match")
		return false;
	}
	  
	  
    setError(null);
    setLoading(true);
	axios.post(getURL() + `users/signin`, { username: username.value, password: password.value, regname: regname.value, regpassword: regpassword.value }).then(response => {
      setLoading(false);
      setUserSession(response.data.token, response.data.user);
	  showDashboard();
      props.history.push('/dashboard');
	  hideLogin();
	  showLogout();
    }).catch(error => {
      setLoading(false);
      if (error.response.status === 401) setError(error.response.data.message);
      else setError("Something went wrong. Please try again later.");
    });
  }
  
  const handleNewRegister = () => {
    props.history.push('/register');
  }
  
  const handleSignIn = () => {
    props.history.push('/login');
  }
	
	function checkPassword(e){
		//alert(e.target.value);
		if(BLUR2){
			BLUR2 = false;
			return true;
		}
		BLUR1 = true;
		let pass = e.target.value;
		if(pass == ""){
			alert("you must enter a password");
			//document.all("txtPassword").focus();
			return true;
		}
		if(regpassword.value.length < 12){
			alert("Your password must be at least 12 characters long");
			//document.all("txtPassword").focus();
			return false;
		}
		FIRSTONE = e.target.value;
		if(C_STRONGREGEX.test(pass)){
			document.all("txtPassword").style.background = "green";
			return true;
		}
		else if(C_MEDIUMREGEX.test(pass)){
			document.all("txtPassword").style.background = "orange";
			alert("password strength is weak");
		}
		else{
			document.all("txtPassword").style.background = "red";
			alert("password strength is too weak to use");
			document.all("txtPassword").value = "";
			//document.all("txtPassword").focus();
		}
		return true;
	}
	
	function checkPassword2(e){
	document.all("divLogo").style.visibility = "visible";
		if(BLUR1){
			BLUR1 = false;
			return true;
		}
		BLUR2 = true;
		if(document.all("txtPassword").value !== ""){
			if(document.all("txtPassword").value !== document.all("txtPassword2").value){
				//document.all("txtPassword2").style.background = "red";
				alert("the passwords do not match");
				//document.all("txtPassword").focus();
				return false;
			}
			else {
				//document.all("txtPassword2").style.background = "green";
			}
		}
		return true;
	}
 
  return (
    <div>
	<div>
	 </div>
	 <div id="divRegister" style={{visibility: "visible"}}>
	 <table>
	  <tr>
	  <td colspan="2">
	  Register <br />
	  </td>
	  </tr>
	  <tr>
	  <td>
		<span className="notes">Email</span>
	  </td>
	  <td>
		<input id="txtEmail" type="text" {...regname} />
	  </td>
	  </tr>
	  <tr>
	  <td>
		<span className="notes">Password</span>
	  </td>
	  <td>
		<input id="txtPassword" type="password" {...regpassword} onBlur={checkPassword} />
	  </td>
	  </tr>
	  <tr>
	  <td colspan="2">
		<span className="notes-small">NB: A strong password must contain a combination of: uppercase letters; lower case letters; special characters; numbers and be at least 12 characters long.</span>
	  </td>
	  </tr>
	  <tr>
	  <td>
		<span className="notes">Confirm password</span>
	  </td>
	  <td>
		<input id="txtPassword2" type="password" {...regpassword2} onBlur={checkPassword2}/>
	  </td>
	  </tr>
	  <tr>
	  <td colspan="2">
	  <input type="button" value="Register" onClick={handleLogin} disabled={loading} /> <br />
	  </td>
	  </tr>
	  </table>
	 </div>
	 
	  <br />{error && <><small style={{ color: 'red' }}>{error}</small><br /></>}<br />
	</div>
  );
}
 
const useFormInput = initialValue => {
  const [value, setValue] = useState(initialValue);
 
  const handleChange = e => {
    setValue(e.target.value);
  }
  return {
    value,
    onChange: handleChange
  }
}
 
export default Register;