// return the user data from the session storage
export const getUser = () => {
  const userStr = sessionStorage.getItem('user');
  if (userStr) return JSON.parse(userStr);
  else return null;
}
 
// return the token from the session storage
export const getToken = () => {
  return sessionStorage.getItem('token') || null;
}
 
// remove the token and user from the session storage
export const removeUserSession = () => {
  sessionStorage.removeItem('token');
  sessionStorage.removeItem('user');
}
 
// set the token and user from the session storage
export const setUserSession = (token, user) => {
  sessionStorage.setItem('token', token);
  sessionStorage.setItem('user', JSON.stringify(user));
}

export const hideLogo = () => {
    document.all("divLogo").style.visibility = "hidden";
}

export const showLogo = () => {
	try{
    document.all("divLogo").style.visibility = "visible";
	}
	catch(e){}
}

export const showLogin = () => {
    document.all("divLogin").style.visibility = "visible";
    document.all("divLogin").style.width = "80px";
	document.all("divLogout").style.visibility = "hidden";
	  try{
		showLogo();
	  }
	  catch(e){}
}

export const hideLogin = () => {
    document.all("divLogin").style.visibility = "hidden";
    document.all("divLogin").style.width = "0px";
}

export const showLogout = () => {
	document.all("divLogout").style.visibility = "visible";
	document.all("divLogout").style.width = "150px";
}

export const hideLogout = () => {
	document.all("divLogout").style.visibility = "hidden";
	document.all("divLogout").style.width = "0px";
}

export const showSecureTabs = () => {
    document.all("divDashboard").style.visibility = "visible";
	document.all("divDashboard").style.width = "130px";
	showLogout();
}

export const hideSecureTabs = () => {
	document.all("divDashboard").style.visibility = "hidden";
	document.all("divDashboard").style.width = "0px";
	document.all("divLogout").style.visibility = "hidden";
	document.all("divLogout").style.width = "0px";
	showLogin();
}

export const showDashboard = () => {
    document.all("divDashboard").style.visibility = "visible";
	document.all("divDashboard").style.width = "130px";
}
 
