import React, { useState } from 'react';
import axios from 'axios';
import { removeUserSession, hideSecureTabs, hideLogout, showLogo } from './Utils/Common';
 
function Logout(props) {
	showLogo();
  const [loading, setLoading] = useState(false);
  const username = useFormInput('');
  const password = useFormInput('');
  const regname = useFormInput('');
  const regpassword = useFormInput('');
  const regpassword2 = useFormInput('');
  const [error, setError] = useState(null);
   
  const handleNewRegister = () => {
    props.history.push('/register');
  }
  
  // handle click event of logout button
  const handleLogout = () => {
	hideSecureTabs();
    removeUserSession();
    props.history.push('/login');
  }
 
  return (
    <div>
    <div>
      <input type="button" onClick={handleLogout} value="Confirm logout" />
    </div>
	</div>
  );
}
 
const useFormInput = initialValue => {
  const [value, setValue] = useState(initialValue);
 
  const handleChange = e => {
    setValue(e.target.value);
  }
  return {
    value,
    onChange: handleChange
  }
}
 
export default Logout;