import React, { useState, useEffect } from 'react';
import { BrowserRouter, Switch, Route, NavLink } from 'react-router-dom';
import { getURL } from './Utils/Common';
import axios from 'axios';
 
import Login from './Login';
import Register from './Register';
import Dashboard from './Dashboard';
import Home from './Home';
import Logout from './Logout';
import logo from './logo.png';
 
import PrivateRoute from './Utils/PrivateRoute';
import PublicRoute from './Utils/PublicRoute';
import { getToken, removeUserSession, setUserSession } from './Utils/Common';

//import {ReactComponent as AALogo} from './logo.svg';
 
function App() {
  const [authLoading, setAuthLoading] = useState(true);
 
  useEffect(() => {
    const token = getToken();
    if (!token) {
      return;
    }
 
    //axios.get(`http://localhost:4000/verifyToken?token=${token}`).then(response => {
    axios.post(getURL() + `verifyToken?token=${token}`).then(response => {
      setUserSession(response.data.token, response.data.user);
      setAuthLoading(false);
    }).catch(error => {
      removeUserSession();
      setAuthLoading(false);
    });
  }, []);
 
  if (authLoading && getToken()) {
    return <div className="content">Checking Authentication...</div>
  }
 
  return (
    <div className="App">
      <BrowserRouter>
		<center>
        <div id="divNav">
			  <div className="header">
				<table>
					<tr>
						<td><NavLink exact activeClassName="active" to="/">Home</NavLink></td>
						<td>
							<div id="divLogin" style={{visibility: "visible", width: "80px"}}>
								<NavLink activeClassName="active" to="/login">Login</NavLink><small></small>
							</div>
						</td>
						<td>
							<div id="divDashboard" style={{visibility: "hidden", width: "0px"}}>
								<NavLink activeClassName="active" to="/dashboard">My Stuff</NavLink><small></small>
							</div>
						</td>
						<td>
							<div id="divLogout" style={{visibility: "hidden", width: "0px"}}>
								<NavLink activeClassName="active" to="/logout">Logout</NavLink><small></small>
							</div>
						</td>
					</tr>
				</table>
			  </div>
			  <div className="content">
				<Switch>
				  <Route exact path="/" component={Home} />
				  <PublicRoute path="/login" component={Login} />
				  <PublicRoute path="/register" component={Register} />
				  <PrivateRoute path="/dashboard" component={Dashboard} />
				  <PrivateRoute path="/logout" component={Logout} />
				</Switch>
			  </div>
			</div>
		</center>
      </BrowserRouter>
	  <div id="divLogo" className="logo">
		<img src={logo} />
	  </div>

    </div>
	
  );
}
 
export default App;